.ProjectHeader-IconLink {
  opacity: 0.5;
  padding: 8px;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: -8px;
}

.ProjectHeader-CompanyTitle {
  display: -ms-flexbox;
