.Projects {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}

.Projects-Container {
  width: 100%;
}

.Projects-Project {
  margin-bottom: 20px;
}

.Projects-SubSection{ 
  padding: 0px 20px 15px;
}

.Projects-Description {
  margin-left: 10px;
  color: #666;
}

.Projects-SubHeader {
  margin-bottom: 10px;
}

.Projects-ListItems {
  margin-left: 10px;
  color: #666;
}

.Projects-ListItem {
  margin-bottom: 7px
}

.Projects-Gallery {
  display: -ms-flexbox;
  display: flex;
  overflow-x: scroll;
  margin: 0 10px;
}

.Projects-GalleryImg {
  height: 200px;
  margin-right: 10px;
  border: solid 1px #aaa;
  opacity: 0.7;
  -webkit-transition-duration: .3s;
       -o-transition-duration: .3s;
          transition-duration: .3s;
  -webkit-transition-property: opacity filter;
  -o-transition-property: opacity filter;
  transition-property: opacity filter;
  -webkit-filter: grayscale(80%);
  filter: grayscale(80%);
}

.Projects-GalleryImg:hover, .Projects-GalleryImg:active {
  opacity: 1;
  -webkit-filter: none;
          filter: none;
}

@media print {
  .Projects {
    display: block;
  }

  .Projects-Project {
    page-break-after: always;
  }

  .Projects-Project:last-child {
    page-break-after: auto;
  }

  .Projects-Gallery {
    display: block;
    overflow: auto;
  }

  .Projects-GalleryImg {
    height: 150px;
    display: inline-block;
    margin-bottom: 10px;
    opacity: 1;
    -webkit-transition-property: none;
    -o-transition-property: none;
    transition-property: none;
    -webkit-filter: none;
    filter: none;
  }
}



