.PersonalData {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}

.PersonalData-Profile {
  width: 100%;
  height: 100%;
  max-width: 180px;
  max-height: 180px;
  border-radius: 140px;
  overflow: hidden;
  margin-bottom: 30px
}

.PersonalData-Profile > img {
  width: inherit;
}

.PersonalData-Row {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0px 20px 15px;
}

.PersonalData-Title {
  font-size: 0.8rem;
  color: #888;
  margin-bottom: 5px;
}

@media screen and (min-width: 480px) {
  .PersonalData-Profile {
    max-width: 240px;
    max-height: 240px;
  }

  .PersonalData-Row {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .PersonalData-Title {
    font-size: 1rem;
  }

  .PersonalData-Row > *:nth-child(1) {
    -ms-flex-preferred-size: 170px;
        flex-basis: 170px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
  }
}


