.Education {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}

.Education-Row {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0px 20px 15px;
}

.Education-Daterange {
  font-size: 0.8rem;
  margin-bottom: 3px;
  color: #888;
}

.Education-Note {
  margin-left: 20px;
  font-size: 0.9rem;
  color: #666;
}

@media screen and (min-width: 480px) {
  .Education-Row {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .Education-Daterange {
    font-size: 1rem;
  }

  .Education-Row > *:nth-child(1) {
    -ms-flex-preferred-size: 170px;
        flex-basis: 170px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
  }
}


